import { Button, Grid, MobileStepper, Step, StepLabel, Stepper, useTheme } from "@mui/material";
import { FormSteps } from "@type/dsp";
import * as React from "react";
import _findIndex from "lodash/findIndex";
import { KeyboardArrowLeft, KeyboardArrowRight } from "@mui/icons-material";

type Props = {
  formStep: number;
};

const stepSet = [
  {
    label: "Preview",
    steps: [FormSteps.intro, FormSteps.selectSiteAsset, FormSteps.previewCreative],
  },
  {
    label: "Audience",
    steps: [FormSteps.siteTargeting],
  },
  {
    label: "Details & Payment",
    steps: [FormSteps.budgetTiming, FormSteps.pay, FormSteps.config, FormSteps.confirmAd],
  },
  {
    label: "Finish!",
    steps: [FormSteps.finish],
  },
];

export const WidgetStepper: React.FC<Props> = ({ formStep }) => {
  const currentIndex = _findIndex(stepSet, e => e.steps.includes(formStep)) || 0;

  return (
    <Grid item xs={12}>
      <Stepper
        sx={{
          mb: 2,
        }}
        activeStep={currentIndex}
        alternativeLabel
      >
        {stepSet.map(step => {
          const label = step.label;
          return (
            <Step key={label}>
              <StepLabel>{label}</StepLabel>
            </Step>
          );
        })}
      </Stepper>
    </Grid>
  );
};

export const MobileWidgetStepper: React.FC<Props> = ({ formStep }) => {
  const [activeStep, setActiveStep] = React.useState(0);
  const currentIndex = _findIndex(stepSet, e => e.steps.includes(formStep)) || 0;
  const theme = useTheme();

  const handleNext = () => {
    setActiveStep(prevActiveStep => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1);
  };

  return (
    <Grid item xs={12}>
      <MobileStepper
        sx={{
          mb: 2,
        }}
        activeStep={currentIndex}
        variant="text"
        steps={stepSet.length}
        position="static"
        nextButton={
          <Button size="small" onClick={handleNext} disabled={currentIndex === stepSet.length - 1}>
            Next
            {theme.direction === "rtl" ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
          </Button>
        }
        backButton={
          <Button size="small" onClick={handleBack} disabled={currentIndex === 0}>
            {theme.direction === "rtl" ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
            Back
          </Button>
        }
      />
    </Grid>
  );
};
