import * as React from "react";
import Grid from "@mui/material/Grid";
import { Button, Slide, SlideProps } from "@mui/material";
import { BuilderTitle } from "@components/adBuilder/components/BuilderTitle";
import { RootState } from "@app/store";
import { connect, ConnectedProps } from "react-redux";
import { setPaymentMethod } from "@app/adBuilderSlice";
import { useEffect, useRef, useState } from "react";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import "@power-elements/stripe-elements";

/**
 * Stripe token looks like this:
 * {
  "id": "tok_1LAkCwHQRXl8YKderG1C6qPX",
  "object": "token",
  "card": {
    "id": "card_1LAkCwHQRXl8YKdeZiFdpsy1",
    "object": "card",
    "address_city": null,
    "address_country": null,
    "address_line1": null,
    "address_line1_check": null,
    "address_line2": null,
    "address_state": null,
    "address_zip": "95945",
    "address_zip_check": "unchecked",
    "brand": "Visa",
    "country": "US",
    "cvc_check": "unchecked",
    "dynamic_last4": null,
    "exp_month": 3,
    "exp_year": 2027,
    "funding": "credit",
    "last4": "5302",
    "name": null,
    "tokenization_method": null
  },
  "client_ip": "73.41.206.228",
  "created": 1655253342,
  "livemode": false,
  "type": "card",
  "used": false
}
 */

type StripeCard = {
  id: string;
  object: string; // card
  address_city: string | null;
  address_country: string | null;
  address_line1: string | null;
  address_line1_check: string | null;
  address_line2: string | null;
  address_state: string | null;
  address_zip: string;
  address_zip_check: string;
  brand: string;
  country: string;
  cvc_check: string;
  dynamic_last4: string | null;
  exp_month: number;
  exp_year: number;
  funding: string;
  last4: string;
  name: string | null;
  tokenization_method: string | null;
};

type StripeToken = {
  id: string;
  object: string; // token
  card: StripeCard;
  client_ip: string;
  created: number;
  livemode: boolean;
  type: string; // card
  used: boolean;
};

type StripeTokenResponse = {
  token: StripeToken;
};

type StripeErrorEvent = ErrorEvent & {
  detail?: StripeError;
};

type StripeError = string; // maybe?

// This interface describes our custom element, holding all its
// available attributes. This should be placed within a .d.ts file.
declare interface StripeElementsComponent extends HTMLElement {
  checked: boolean;
  createToken: () => Promise<StripeTokenResponse>;
}

declare global {
  namespace JSX {
    interface IntrinsicElements {
      "stripe-elements": React.DetailedHTMLProps<
        React.HTMLAttributes<StripeElementsComponent>,
        StripeElementsComponent
      >;
    }
  }
}

type CheckoutFormProps = {
  stripeKey: string;
};

const CheckoutForm: React.FC<CheckoutFormProps> = ({ stripeKey }) => {
  const stripeRef = useRef<StripeElementsComponent>(null);
  const [disabled, setDisabled] = useState(true);
  const [token, setToken] = useState<StripeToken | null>(null);
  const [error, setError] = useState<StripeError | null>(null);

  const onChange = (event: any) => {
    console.warn("on change", event);
    const el = event.target;
    setDisabled(!el.complete || el.empty);
  };

  const onError = (event: StripeErrorEvent) => {
    console.warn("on error", event);
    setError(event.detail || "Unknown error");
  };

  const onToken = (event: any) => {
    console.warn("on token", event);
    setToken(event.token);
  };

  //compose(setDisabled, isDisabled, getTarget);

  useEffect(() => {
    if (stripeRef.current) {
      stripeRef.current.addEventListener("change", onChange);
      stripeRef.current.addEventListener("error", onError);
      stripeRef.current.addEventListener("token", onToken);
      // if (token || error) viewerRef.current.object = token;

      return () => {
        if (stripeRef.current) {
          stripeRef.current.removeEventListener("change", onChange);
          stripeRef.current.removeEventListener("error", onError);
          stripeRef.current.removeEventListener("token", onToken);
        }
      };
    }
  }, []);

  const handleClick = async (event: any) => {
    if (stripeRef.current) {
      const token = await stripeRef.current.createToken();
      setToken(token.token);
    }
  };

  return (
    <>
      <stripe-elements ref={stripeRef} publishable-key={stripeKey} />
      <Button
        variant="contained"
        type="submit"
        disabled={disabled}
        onClick={handleClick}
        startIcon={<AttachMoneyIcon />}
      >
        Pay
      </Button>
      {token && <div>Token: {JSON.stringify(token)}</div>}
      {error && <div>Error: {JSON.stringify(error)}</div>}
    </>
  );
};

const slideProps: SlideProps = {
  easing: {
    enter: "linear",
    exit: "inherit",
  },
  direction: "left",
  mountOnEnter: true,
  unmountOnExit: true,
} as SlideProps;

type Props = ConnectedProps<typeof connector>;

export const StepPayComponent: React.FC<Props> = ({ stripeKey }) => {
  return (
    <Slide in={true} {...slideProps}>
      <Grid container spacing={2}>
        <BuilderTitle title={"Payment Details"} />
        <Grid item xs={12}>
          <CheckoutForm stripeKey={stripeKey} />
        </Grid>
      </Grid>
    </Slide>
  );
};

const mapDispatchToProps = { setPaymentMethod };
const mapStateToProps = (state: RootState) => ({
  stripeKey: state.adBuilder.widgetParams.stripeKey,
});
const connector = connect(mapStateToProps, mapDispatchToProps);
export const StepPay = connector(StepPayComponent);
