import CircularProgress from "@mui/material/CircularProgress";
import LinearProgress from "@mui/material/LinearProgress";

const enum LoadingTypes {
  default,
  linear,
}

type LoadingProps = {
  type?: LoadingTypes;
  showText?: boolean;
  size?: number;
  centered?: boolean;
};

export const Loading = (props: LoadingProps) => {
  const { type = LoadingTypes.default, showText = false, size, centered } = props;

  const LoadingComponent =
    type === LoadingTypes.default ? <CircularProgress size={size} /> : <LinearProgress style={{ width: size || 30 }} />;

  return (
    <div
      style={{
        display: "flex",
        height: "100%",
        ...(centered && { alignItems: "center" }),
        justifyContent: "center",
        margin: "0 10px 0 10px",
      }}
    >
      {LoadingComponent}
      <span style={{ marginLeft: `${(size || 30) / 2}px` }}>{showText && "Loading..."}</span>
    </div>
  );
};
