import { Campaign, Site, UrnData, WPCOMPost } from "@type/dsp";

export const validateCampaignToSync = (campaign: Campaign): { valid: boolean; reason: string } => {
  const valid = [campaign.name, campaign.description, campaign.start_date, campaign.end_date].every(el => el);
  let reason = "unknown";
  if (!campaign.start_date || !campaign.end_date) {
    reason = "Invalid dates";
  }

  return {
    valid,
    reason,
  };
};

export const truncateText = (text: string, maxchars: number) => {
  return text.length > maxchars ? text.substring(0, maxchars) + "..." : text;
};

export const extractTextFromHTMLTag = (text: string) => {
  const div = document.createElement("div");
  div.innerHTML = text;
  const textDescription = div.textContent || div.innerText || "";
  return textDescription;
};

export const cropAdText = (text: string, characters = 70) => {
  const stripped = extractTextFromHTMLTag(text);
  return stripped.length > characters ? truncateText(stripped, characters) : stripped;
};

export const extractWooSalePriceFromHTMLTag = (text: string) => {
  const div = document.createElement("div");
  div.innerHTML = text;
  const priceDiv = div.querySelector("ins");
  const textDescription = priceDiv?.textContent || priceDiv?.innerText || "";
  return textDescription;
};

export const getUrnForWPComPost = (site: Site, post: WPCOMPost) => {
  return `urn:wpcom:post:${site.ID}:${post.ID}`;
};

// Todo: we should immitate methods used in Wordpress
export const formatPriceInCents = (number: number, currency = "$"): string => {
  const numberFormatted = (number / 100).toFixed(2);
  // Todo: Quite basic, TODO: Improve
  if (currency === "$") {
    return `${currency}${numberFormatted}`;
  } else {
    return `${numberFormatted}${currency}`;
  }
};

export const getDataFromUrn = (urn: string): UrnData => {
  if (!urn) {
    return { siteId: 0, postId: 0 };
  }

  const siteId = +urn.split(":")[3];
  const postId = +urn.split(":")[4];
  return { siteId, postId };
};
