import { BaseQueryFn, createApi, FetchArgs, fetchBaseQuery, FetchBaseQueryError } from "@reduxjs/toolkit/query/react";
import {
  // Ad
  AdRequest,
  AdResponse,
  CreateAdRequest,
  DeleteAdRequest,
  UpdateAdRequest,
  // Campaign
  CampaignAdsRequest,
  CampaignAdsResponse,
  CampaignRequest,
  CampaignResponse,
  CampaignsRequest,
  CampaignsResponse,
  CreateCampaignRequest,
  UpdateCampaignRequest,
  DeleteCampaignRequest,
  // Creative
  CreativeRequest,
  CreativeResponse,
  CreativesRequest,
  CreativesResponse,
  UpdateCreativeRequest,
  DeleteCreativeRequest,
  // User info
  UserInfoRequest,
  UserInfoResponse,
  UserSitesRequest,
  UserSitesResponse,
  // WP Site Posts
  SitePostsResponse,
  SitePostsRequest,
  // Continents, countries and regions
  AvailableContinentsRequest,
  AvailableContinentsResponse,
  MediaResponse,
  MediaRequest,
  SitePostResponse,
  SitePostRequest,
  AvailableSiteLanguagesResponse,
  AvailableSiteTopicsResponse,
  LocationsResponse,
  UserAuthCheckResponse,
  CampaignsFullResponse,
  SubmitCampaignRequest,
} from "@type/dsp";
import { RootState } from "./store";

export const getCuratedURN = (urn: string): string => {
  let curatedURN = decodeURIComponent(urn); // Prevent encoded urns
  const isTumblrPost = urn.includes("tumblr:post");
  if (isTumblrPost) {
    const idParts = curatedURN.split(":");
    const blogId = idParts[4] || "";
    const postId = idParts[5] || "";
    const encodedTumblogid = encodeURIComponent(`t:${blogId}`);
    curatedURN = encodeURIComponent(`urn:tumblr:post:${encodedTumblogid}:${postId}`);
  }
  return curatedURN;
};

const dynamicBaseQuery: BaseQueryFn<string | FetchArgs, unknown, FetchBaseQueryError> = async (
  args,
  WebApi,
  extraOptions
) => {
  const apiHost =
    (WebApi.getState() as RootState).adBuilder.widgetParams.apiHost || process.env.REACT_APP_DSP_API_HOST || "";
  const apiPrefix =
    (WebApi.getState() as RootState).adBuilder.widgetParams.apiPrefix || process.env.REACT_APP_DSP_API_PREFIX || "";
  const baseUrl = `${apiHost}${apiPrefix}${BASE_URI}`;

  const rawBaseQuery = fetchBaseQuery({
    baseUrl,
    prepareHeaders: (headers, { getState }) => {
      // only accept application/json
      headers.set("accept", "application/json");
      const token = (getState() as RootState).adBuilder.widgetParams.authToken;

      // If we have a token set in state, let's assume that we should be passing it.
      if (token) {
        if (token.startsWith("rlt-")) {
          // special handling for 'rlt-' token
          headers.set("Authorization", `X_WPCOM_RLT ${token}`);
        } else {
          headers.set("Authorization", `Bearer ${token}`);
        }
      }

      return headers;
    },
  });
  return rawBaseQuery(args, WebApi, extraOptions);
};

// Define a service using a base URL and expected endpoints
export const BASE_URI = "/api/v1";
export const dspApi = createApi({
  reducerPath: "dspAPI",
  tagTypes: [
    "Campaigns",
    "CampaignsFull",
    "Ads",
    "Creatives",
    "AssetsSites",
    "AssetsPosts",
    "AssetsProducts",
    "Continents",
    "SiteLanguages",
    "SiteTopics",
    "Locations",
    "User",
  ],
  baseQuery: dynamicBaseQuery,
  endpoints: builder => {
    return {
      getUserInfo: builder.query<UserInfoResponse, UserInfoRequest>({
        query: (request: UserInfoRequest) => {
          return {
            url: "/user",
          };
        },
        providesTags: ["User"],
      }),

      getCampaigns: builder.query<CampaignsResponse, CampaignsRequest>({
        query: (request: CampaignsRequest) => {
          return {
            url: "/campaigns",
          };
        },
        providesTags: ["Campaigns"],
      }),
      getCampaignsFull: builder.query<CampaignsFullResponse, CampaignsRequest>({
        query: (request: CampaignsRequest) => {
          return {
            url: "/campaigns/full",
          };
        },
        providesTags: ["CampaignsFull"],
      }),

      getCampaign: builder.query<CampaignResponse, CampaignRequest>({
        query: (request: CampaignRequest) => {
          return {
            url: `/campaigns/${request.campaign_id}`,
          };
        },
        providesTags: ["Campaigns"],
      }),

      createCampaign: builder.mutation<CampaignResponse, CreateCampaignRequest>({
        query: (request: CreateCampaignRequest) => ({
          url: "/campaigns",
          method: "POST",
          body: request,
        }),
        invalidatesTags: ["Campaigns"],
      }),

      editCampaign: builder.mutation<CampaignResponse, UpdateCampaignRequest>({
        query: (campaign: UpdateCampaignRequest) => ({
          url: `/campaigns/${campaign.campaign_id}`,
          method: "PATCH",
          body: campaign,
        }),
        invalidatesTags: ["Campaigns"],
      }),

      deleteCampaign: builder.mutation<CampaignResponse, DeleteCampaignRequest>({
        query: (campaign: DeleteCampaignRequest) => ({
          url: `/campaigns/${campaign.campaign_id}`,
          method: "DELETE",
        }),
        invalidatesTags: ["Campaigns"],
      }),

      stopCampaign: builder.mutation<SubmitCampaignRequest, DeleteCampaignRequest>({
        query: (campaign: DeleteCampaignRequest) => ({
          url: `/campaigns/${campaign.campaign_id}/stop`,
          method: "POST",
        }),
        invalidatesTags: ["Campaigns"],
      }),

      createAd: builder.mutation<AdResponse, CreateAdRequest>({
        query: (request: CreateAdRequest) => {
          return {
            url: `/campaigns/${request.campaign_id}/ads`,
            method: "POST",
            body: request,
          };
        },
        invalidatesTags: ["Campaigns", "Ads"],
      }),

      editAd: builder.mutation<AdResponse, UpdateAdRequest>({
        query: (request: UpdateAdRequest) => ({
          url: `/campaigns/${request.campaign_id}/ads/${request.ad_id}`,
          method: "PATCH",
          body: request,
        }),
        invalidatesTags: ["Campaigns", "Ads"],
      }),

      deleteAd: builder.mutation<AdResponse, DeleteAdRequest>({
        query: (request: DeleteAdRequest) => ({
          url: `/campaigns/${request.campaign_id}/ads/${request.ad_id}`,
          method: "DELETE",
        }),
        invalidatesTags: ["Ads"],
      }),

      getAds: builder.query<CampaignAdsResponse, CampaignAdsRequest>({
        query: (request: CampaignAdsRequest) => {
          return {
            url: `/campaigns/${request.campaign_id}/ads`,
          };
        },
        providesTags: ["Ads"],
      }),

      getAd: builder.query<AdResponse, AdRequest>({
        query: (request: AdRequest) => {
          return {
            url: `/campaigns/${request.campaign_id}/ads/${request.ad_id}`,
          };
        },
        providesTags: ["Campaigns"],
      }),

      getCreatives: builder.query<CreativesResponse, CreativesRequest>({
        query: (request: CreativesRequest) => {
          return {
            url: `/campaigns/${request.campaign_id}/ads/${request.ad_id}/creatives`,
          };
        },
        providesTags: ["Creatives"],
      }),

      getCreative: builder.query<CreativeResponse, CreativeRequest>({
        query: (request: CreativeRequest) => {
          return {
            url: `/campaigns/${request.campaign_id}/ads/${request.ad_id}/creatives/${request.creative_id}`,
          };
        },
        providesTags: ["Creatives"],
      }),

      deleteCreative: builder.mutation<CreativeResponse, DeleteCreativeRequest>({
        query: (creative: DeleteCreativeRequest) => ({
          url: `/campaigns/${creative.campaign_id}/ads/${creative.ad_id}/creatives/${creative.creative_id}`,
          method: "DELETE",
        }),
        invalidatesTags: ["Creatives"],
      }),

      editCreative: builder.mutation<CreativeResponse, UpdateCreativeRequest>({
        query: (creative: UpdateCreativeRequest) => ({
          url: `/campaigns/${creative.campaign_id}/ads/${creative.ad_id}/creatives/${creative.creative_id}`,
          method: "PATCH",
          body: creative,
        }),
        invalidatesTags: ["Creatives"],
      }),

      getSites: builder.query<UserSitesResponse, UserSitesRequest>({
        query: (request: UserSitesRequest) => {
          return {
            url: "/wpcom/sites",
          };
        },
        providesTags: ["AssetsSites"],
      }),

      getSitePosts: builder.query<SitePostsResponse, SitePostsRequest>({
        query: (request: SitePostsRequest) => {
          const { site_id, post_type, pagination } = request;
          const { page, limit, page_handle } = pagination;
          return {
            url: `/wpcom/sites/${site_id}/posts?${
              (page_handle ? `page_handle=${page_handle}` : page ? `page=${page}&limit=${limit}` : "") +
              (post_type ? `&post_type=${post_type}` : "")
            }`,
          };
        },
        providesTags: ["AssetsPosts"],
      }),

      getSitePost: builder.query<SitePostResponse, SitePostRequest>({
        query: (request: SitePostRequest) => {
          const { site_id, post_id } = request;
          return {
            url: `/wpcom/sites/${site_id}/posts/${post_id}`,
          };
        },
        providesTags: ["AssetsPosts"],
      }),

      getSiteAssets: builder.query<MediaResponse, MediaRequest>({
        query: (request: MediaRequest) => {
          const { site_id, post_id } = request;
          return {
            url: `/wpcom/sites/${site_id}/posts/${post_id}/media`,
          };
        },
        providesTags: ["AssetsPosts"],
      }),

      getSmartAvailableContinents: builder.query<AvailableContinentsResponse, AvailableContinentsRequest>({
        query: () => {
          return {
            url: "/smart/continents",
            method: "GET",
          };
        },
        providesTags: ["Continents"],
      }),

      getAvailableSiteTargetLanguages: builder.query<AvailableSiteLanguagesResponse, void>({
        query: () => {
          return {
            url: "/smart/target/site/languages",
            method: "GET",
          };
        },
        providesTags: ["SiteLanguages"],
      }),

      getAvailableSiteTargetTopics: builder.query<AvailableSiteTopicsResponse, void>({
        query: () => {
          return {
            url: "/smart/target/site/topics",
            method: "GET",
          };
        },
        providesTags: ["SiteTopics"],
      }),

      getAvailableLocations: builder.query<LocationsResponse, void>({
        query: () => {
          return {
            url: "/locations",
            method: "GET",
          };
        },
      }),

      getHelloWorld: builder.query<{ hello: string }, { name: string }>({
        query: (request: { name: string }) => {
          return {
            url: "/helloWorld",
            params: request,
          };
        },
      }),
      userAuthCheck: builder.query<UserAuthCheckResponse, void>({
        query: () => {
          return {
            url: `/user/check`,
            method: "GET",
          };
        },
        providesTags: ["User"],
      }),
    };
  },
});

// Export hooks for usage in functional components, which are
// auto-generated based on the defined endpoints
export const {
  useGetUserInfoQuery,
  useGetHelloWorldQuery,
  useUserAuthCheckQuery,
  useGetCampaignQuery,
  useGetAdsQuery,
  useGetAdQuery,
  useGetCampaignsQuery,
  useGetCampaignsFullQuery,
  useCreateCampaignMutation,
  useDeleteCampaignMutation,
  useStopCampaignMutation,
  useEditCampaignMutation,
  useCreateAdMutation,
  useDeleteAdMutation,
  useEditAdMutation,
  useGetCreativesQuery,
  useGetCreativeQuery,
  useDeleteCreativeMutation,
  useEditCreativeMutation,
  // assets
  useGetSitesQuery,
  useGetSitePostsQuery,
  useGetSitePostQuery,
  useGetSiteAssetsQuery,
  useGetSmartAvailableContinentsQuery,
  useGetAvailableSiteTargetLanguagesQuery,
  useGetAvailableSiteTargetTopicsQuery,
  useGetAvailableLocationsQuery,
} = dspApi;
