import React from "react"; // Required for the server to understand the JSX
import { CreativeAssetArticleContent } from "@type/dsp";

// These imports will be called in a wrapper component (not exposed to the server) in the UI
// As it is impossible to make node to ignore css files unless we add babel or webpack to create a loader
// import "./reset.css";
// import "./ad_300_250_styles.css"; //

export const AdContainer = (props: any) => {
  return <div className="ad-container">{props.children}</div>;
};

export const AdPostTitle = (props: CreativeAssetArticleContent) => {
  return (
    <div className="ad-snippet">
      <p>{props.snippet}</p>
    </div>
  );
};

export const AdImage = (props: CreativeAssetArticleContent) => {
  return (
    <div className="ad-image">
      {props.imageUrl ? (
        <img style={{ width: "100%" }} src={props.imageUrl} alt={`${props.title} image`} />
      ) : (
        <div className="no-image">
          
          <br />
          <br />
          Please upload an image
        </div>
      )}
    </div>
  );
};

export const AdSiteTitle = (props: CreativeAssetArticleContent) => {
  return (
    <div className="ad-header">
      <div className="ad-header-title" style={{}}>
        {props.title}
      </div>
      <div className="ad-header-subtitle">Sponsored</div>
    </div>
  );
};
