import * as React from "react";
import Grid from "@mui/material/Grid";
import { Slide, SlideProps, Slider, TextField, TextFieldProps } from "@mui/material";
import { BuilderTitle } from "@components/adBuilder/components/BuilderTitle";
import DateFnsProvider from "@components/utils/DateFnsProvider";
import { setAdBudgetCentsPerDay, setAdEndDate, setAdStartDate } from "@app/adBuilderSlice";
import Typography from "@mui/material/Typography";
import { formatPriceInCents } from "@utils";
import { parseISO } from "date-fns";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";

const slideProps: SlideProps = {
  easing: {
    enter: "linear",
    exit: "inherit",
  },
  direction: "left",
  mountOnEnter: true,
  unmountOnExit: true,
} as SlideProps;

type StepBudgetTimingForm = {
  dailyBudgetInCents: number;
  startDate: string;
  endDate: string;
};

type StepBudgetTimingProps = {
  onSubmit: (values: StepBudgetTimingForm) => void;
  onClickPrevious: () => void;
  dailyBudgetInCents: number;
  startDate: string;
  endDate: string;
  setAdStartDate: typeof setAdStartDate;
  setAdEndDate: typeof setAdEndDate;
  setAdBudgetCentsPerDay: typeof setAdBudgetCentsPerDay;
};

export const StepBudgetTiming: React.FC<StepBudgetTimingProps> = ({
  onClickPrevious,
  onSubmit,
  startDate,
  endDate,
  dailyBudgetInCents,
  setAdStartDate,
  setAdEndDate,
  setAdBudgetCentsPerDay,
}) => {
  return (
    <Slide in={true} {...slideProps}>
      <Grid container>
        <Grid container spacing={2}>
          <DateFnsProvider>
            <BuilderTitle title={"Daily Budget"} />
            <Grid item xs={12}>
              <Typography variant="subtitle1" align="center">
                Typically $5/day yields about 50 new visitors
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Typography variant="h4" textAlign="center">
                {formatPriceInCents(dailyBudgetInCents)} / day
              </Typography>
            </Grid>

            <Grid item xs={12} sx={{ ml: "20%", mr: "20%" }}>
              <Slider
                key="budget"
                step={25}
                min={0}
                max={100 * 50}
                aria-label="Volume"
                value={dailyBudgetInCents}
                onChange={(e, newVal) => {
                  setAdBudgetCentsPerDay(newVal as number);
                }}
              />
            </Grid>

            <Grid item xs={6}>
              <DatePicker
                PopperProps={{ disablePortal: true }}
                onChange={(date, keyboardInputValue) => {
                  const newStartString = (date as unknown as Date).toISOString();
                  setAdStartDate(newStartString);
                  // invalidate end date if needed
                  if (endDate && (date as unknown as Date) > parseISO(endDate)) {
                    setAdEndDate(newStartString);
                  }
                }}
                disablePast
                label="Start Date"
                value={startDate}
                InputProps={{
                  fullWidth: true,
                }}
                renderInput={(params: JSX.IntrinsicAttributes & TextFieldProps) => (
                  <TextField {...params} fullWidth={true} />
                )}
              />
            </Grid>
            <Grid item xs={6}>
              <DatePicker
                PopperProps={{
                  disablePortal: true,
                }}
                disablePast
                onChange={(date, keyboardInputValue) => {
                  setAdEndDate((date as Date).toISOString());
                }}
                value={endDate}
                minDate={parseISO(startDate)}
                label="End Date"
                renderInput={(params: JSX.IntrinsicAttributes & TextFieldProps) => (
                  <TextField {...params} fullWidth={true} />
                )}
              />
            </Grid>
          </DateFnsProvider>
        </Grid>
      </Grid>
    </Slide>
  );
};
