import * as React from "react";
import Grid from "@mui/material/Grid";
import Alert from "@mui/material/Alert";
import {
  Button,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Slide,
  SlideProps,
  Typography,
} from "@mui/material";
import { RootState } from "@app/store";
import { connect, ConnectedProps, useSelector } from "react-redux";
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import RadioButtonUncheckedIcon from "@mui/icons-material/RadioButtonUnchecked";
import Box from "@mui/system/Box";
import { WidgetPublishStatus } from "@app/adBuilderSlice";

const slideProps: SlideProps = {
  easing: {
    enter: "linear",
    exit: "inherit",
  },
  direction: "left",
  mountOnEnter: true,
  unmountOnExit: true,
} as SlideProps;

type Props = ConnectedProps<typeof connector>;

const SuccessMessage: React.FC = () => {
  const campaignCreatedId = useSelector((state: RootState) => state.adBuilder.publishStatus.campaignCreatedId);

  return (
    <>
      <Typography variant="h2" sx={{ mb: 2, mt: 0 }} textAlign="center">
        
      </Typography>
      <Typography variant="h2" sx={{ mb: 3 }} textAlign="center">
        All set
      </Typography>
      <Box sx={{ textAlign: "center", mb: 2 }}>
        <Typography
          variant="h6"
          sx={{ textAlign: "center", mb: 2, display: "inline-block", width: "550px" }}
          textAlign="center"
          component="div"
          color="text.secondary"
        >
          We will contact you shortly once we verify and run the ad
        </Typography>
        <Button
          onClick={e => {
            fetch(`api/v1/smart/campaigns/${campaignCreatedId}/create`, {
              method: "POST",
            });
            const button = e.target as HTMLButtonElement;
            button.textContent = "Campaign sent to smart";
          }}
        >
          Debug: Send campaign to Smart
        </Button>
      </Box>
    </>
  );
};

const PublishProgressItem: React.FC<{ status: boolean; title: string }> = ({ status, title }) => {
  return (
    <ListItem>
      <ListItemButton>
        <ListItemIcon>
          {status ? <CheckCircleOutlineIcon color="success" /> : <RadioButtonUncheckedIcon />}
        </ListItemIcon>
        <ListItemText primary={title} />
      </ListItemButton>
    </ListItem>
  );
};

const PublishProgress: React.FC<{ status: WidgetPublishStatus }> = ({ status }) => {
  return (
    <>
      <List>
        <PublishProgressItem status={status.campaignCreated} title="Campaign Created" />
        <PublishProgressItem status={status.adCreated} title="Ad Created" />
        <PublishProgressItem status={status.assetsUploaded} title="Assets Uploaded" />
        <PublishProgressItem status={status.submitted} title="Submitted for Approval" />
      </List>
    </>
  );
};

export const StepFinishComponent: React.FC<Props> = ({ ad, publishStatus }) => {
  const { errorMessage } = publishStatus;
  const isPublishedSuccessfully =
    publishStatus.campaignCreated && publishStatus.adCreated && publishStatus.assetsUploaded && publishStatus.submitted;

  return (
    <Slide in={true} {...slideProps}>
      <Grid container>
        <Grid item xs={12}>
          {errorMessage && <Alert severity="error">{errorMessage}</Alert>}
        </Grid>
        <Grid item xs={12} sx={{ ml: { lg: 32, md: 32 } }}>
          <PublishProgress status={publishStatus} />
        </Grid>
        <Grid item xs={12}>
          {isPublishedSuccessfully && <SuccessMessage />}
        </Grid>
      </Grid>
    </Slide>
  );
};

const mapDispatchToProps = {};
const mapStateToProps = (state: RootState) => ({
  paymentMethod: state.adBuilder.paymentMethod,
  ad: state.adBuilder.ad,
  formStep: state.adBuilder.formStep,
  publishStatus: state.adBuilder.publishStatus,
});
const connector = connect(mapStateToProps, mapDispatchToProps);
export const StepFinish = connector(StepFinishComponent);
