import * as React from "react";
import { Theme, useTheme } from "@mui/material/styles";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select, { SelectChangeEvent } from "@mui/material/Select";

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
  disablePortal: true, // need to disable Portal since we are using Shadow DOM
};

function getStyles(value: string, selectedValues: string[], theme: Theme) {
  return {
    fontWeight:
      selectedValues.length === 0 || selectedValues.indexOf(value) === -1
        ? theme.typography.fontWeightRegular
        : theme.typography.fontWeightMedium,
  };
}

type MultipleSelectProps = {
  values: string[];
  options: Array<{ value: string; text: string }>;
  onChange: (ids: string[]) => void;
  label?: string;
  placeholder?: string;
};

export default function MultipleSelect({ onChange, values, options, label, placeholder }: MultipleSelectProps) {
  const theme = useTheme();

  const handleChange = (event: SelectChangeEvent<string[]>) => {
    const {
      target: { value },
    } = event;
    onChange(typeof value === "string" ? value.split(",") : value);
  };

  const labelId = `multiple-name-${label}`;
  return (
    <div>
      <FormControl sx={{ m: 0, width: 1 }}>
        <InputLabel id={labelId}>{label}</InputLabel>
        <Select
          labelId={labelId}
          id={`multiple-${label}`}
          multiple
          value={values}
          onChange={handleChange}
          input={<OutlinedInput label={label} />}
          displayEmpty
          MenuProps={MenuProps}
          renderValue={selected => {
            if (selected.length === 0) {
              return <em>{placeholder}</em>;
            }

            return selected.map(value => options.find(option => option.value === value)?.text).join(", ");
          }}
        >
          {options.map(option => (
            <MenuItem
              key={`${label}-${option.value}`}
              value={option.value}
              style={getStyles(option.value, values, theme)}
            >
              {option.text}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </div>
  );
}
