import { configureStore, ThunkAction, Action } from "@reduxjs/toolkit";
import { setupListeners } from "@reduxjs/toolkit/query";
import { dspApi } from "./dspApi";
import { adBuilderSlice } from "@app/adBuilderSlice";

export const store = configureStore({
  reducer: {
    [dspApi.reducerPath]: dspApi.reducer,
    adBuilder: adBuilderSlice.reducer,
  },
  middleware: getDefaultMiddleware => getDefaultMiddleware().concat(dspApi.middleware),
});

// optional, but required for refetchOnFocus/refetchOnReconnect behaviors
// see `setupListeners` docs on https://redux-toolkit.js.org/rtk-query/overview
// - takes an optional callback as the 2nd arg for customization
setupListeners(store.dispatch);

export type AppDispatch = typeof store.dispatch;
export type RootState = ReturnType<typeof store.getState>;
export type AppThunk<ReturnType = void> = ThunkAction<ReturnType, RootState, unknown, Action<string>>;
