import * as React from "react";
import { useEffect, useState } from "react";
import Grid from "@mui/material/Grid";
import { Alert, AlertTitle, Box, Grow, Slide, SlideProps, TextField } from "@mui/material";
import { BuilderTitle } from "@components/adBuilder/components/BuilderTitle";
import {
  fetchImageAsBlob,
  setArticleClickUrl,
  setArticleImageMimeType,
  setArticleImageUrl,
  setArticleSnippet,
  setArticleTitle,
  setNextButtonEnabled,
} from "@app/adBuilderSlice";
import { useGetSitePostQuery } from "@app/dspApi";
import { extractTextFromHTMLTag, getDataFromUrn } from "@utils";
import { RootState } from "@app/store";
import { Loading } from "@components/utils/Loading";
import UploadImageForm from "@components/creative/AddCreativeCard";
import { AdPreviewWrapper } from "./AdPreviewWrapper";
import { connect, ConnectedProps, useDispatch } from "react-redux";
import Ad_300_250 from "@components/creative/adTemplates/ad_300_250";
import { FormContainer } from "react-hook-form-mui";
import { useForm } from "react-hook-form";
import _ from "lodash";

const slideProps: SlideProps = {
  easing: {
    enter: "linear",
    exit: "inherit",
  },
  direction: "left",
  mountOnEnter: true,
  unmountOnExit: true,
} as SlideProps;

type StepPreviewCreativeProps = {
  urn: string;
};

type Props = ConnectedProps<typeof connector>;

const StepPreviewCreativeComponent: React.FC<Props & StepPreviewCreativeProps> = ({ urn, article }) => {
  const { postId, siteId } = getDataFromUrn(urn);
  const [adLoaded, setAdLoaded] = useState(false);
  const dispatch = useDispatch();

  const {
    data: sitePost,
    error: sitePostError,
    isLoading: sitePostIsLoading,
  } = useGetSitePostQuery({
    post_id: postId,
    site_id: siteId,
  });

  const formContext = useForm({
    mode: "all", // re-runs validation onBlur & onChange (default is onSubmit)
  });

  const {
    setValue,
    formState: { errors },
  } = formContext;
  const hasErrors = _.isEmpty(errors);

  // Disable the next button if the form has errors
  useEffect(() => {
    dispatch(setNextButtonEnabled(hasErrors));
  }, [errors, hasErrors]);

  useEffect(() => {
    const downloadWPData = async () => {
      if (sitePost) {
        const { title, excerpt, URL, post_thumbnail } = sitePost;

        let imageUploadedObjectURL;
        // setup blob
        if (post_thumbnail?.URL) {
          const result = await fetchImageAsBlob(post_thumbnail.URL);
          if (result) {
            imageUploadedObjectURL = window.URL.createObjectURL(result);
          }
        }

        setAdLoaded(true);

        // Set form values - this will act as an onChange and subsequently call dispatch
        setValue("title", title);
        setValue("snippet", extractTextFromHTMLTag(excerpt));
        setValue("url", URL);

        // Image isn't managed by the form container - so dispatch these manually
        dispatch(setArticleImageUrl(imageUploadedObjectURL || post_thumbnail?.URL || ""));
        dispatch(setArticleImageMimeType(post_thumbnail?.mime_type || ""));
        dispatch(setArticleTitle(title));
        dispatch(setArticleSnippet(extractTextFromHTMLTag(excerpt)));
        dispatch(setArticleClickUrl(URL));
        dispatch(setNextButtonEnabled(true));
      }
    };
    downloadWPData();
  }, [dispatch, setValue, sitePost]);
  if (sitePostIsLoading || !article) {
    return <Loading centered />;
  }

  // TODO: encapsulate this so we can reuse in other steps
  if (sitePostError) {
    let errorMessage = null;
    if ("status" in sitePostError) {
      // you can access all properties of `FetchBaseQueryError` here
      const responseData = sitePostError.data as { errorMessage: string };
      errorMessage =
        "error" in sitePostError
          ? sitePostError.error
          : "errorMessage" in responseData
          ? responseData.errorMessage
          : JSON.stringify(sitePostError.data);
    } else {
      // you can access all properties of `SerializedError` here
      errorMessage = sitePostError.message || "Please reload the page and try again";
    }

    return (
      <Alert severity="error">
        <AlertTitle>There was an error</AlertTitle>
        {errorMessage}
      </Alert>
    );
  }

  if (!article) {
    return <div>Undefined object</div>;
  }

  return (
    <Slide in={true} {...slideProps}>
      <Grid container>
        <BuilderTitle title={"Let's customize the creative"} />
        <Grid container sx={{ mt: 3 }}>
          <Grid item xs={6}>
            {adLoaded ? (
              <Grow timeout={1000} in>
                <Box>
                  <AdPreviewWrapper>
                    <Ad_300_250 {...article} />
                  </AdPreviewWrapper>
                </Box>
              </Grow>
            ) : (
              <Loading centered />
            )}
          </Grid>

          <Grid item xs={6} sx={{ pl: 2 }}>
            <FormContainer formContext={formContext} onSuccess={() => {}}>
              <TextField
                margin={"normal"}
                label="Title"
                name="title"
                required
                fullWidth
                value={article.title}
                onChange={e => {
                  dispatch(setArticleTitle(e.target.value));
                }}
              />
              <TextField
                margin={"normal"}
                name="snippet"
                label="Snippet"
                required
                multiline
                fullWidth
                rows={2}
                value={article.snippet}
                onChange={e => {
                  dispatch(setArticleSnippet(e.target.value));
                }}
              />

              <TextField
                margin={"normal"}
                name="url"
                label="Target Url"
                required
                fullWidth
                value={article.clickUrl}
                onChange={e => {
                  dispatch(setArticleClickUrl(e.target.value));
                }}
              />
              <Box mt={2}>
                <UploadImageForm
                  onUpload={(imageBlobUrl, mimetype) => {
                    dispatch(setArticleImageUrl(imageBlobUrl));
                    dispatch(setArticleImageMimeType(mimetype));
                    // TODO: proper validation before enabling the button
                    if (!errors) dispatch(setNextButtonEnabled(true));
                  }}
                />
              </Box>
            </FormContainer>
          </Grid>
        </Grid>
      </Grid>
    </Slide>
  );
};

// const mapDispatchToProps = { setFormStep, setTargetUrn, setAdType };
const mapStateToProps = (state: RootState) => ({
  article: state.adBuilder.article,
  urn: state.adBuilder.widgetParams.urn,
});
const connector = connect(mapStateToProps);
export const StepPreviewCreative = connector(StepPreviewCreativeComponent);
