/**
 * An ad
 *
 * based on this schema
 * -- an ad is a single insertion in a campaign
-- TODO: targeting
CREATE TABLE IF NOT EXISTS dsp_ads (
	ad_id INT AUTO_INCREMENT PRIMARY KEY,
	campaign_id INT NOT NULL,
	smart_id VARCHAR(255) DEFAULT NULL, -- the id of the insertion in Smart
	smart_updated_at TIMESTAMP DEFAULT NULL, -- the last time Smart updated this insertion
	name VARCHAR(255),
	description TEXT,
	start_date DATETIME,
	end_date DATETIME,
	budget_cents INT, -- usd in cents, i.e. 1000 = USD$10
	type VARCHAR(20), -- one of 'house', 'self-serve', 'direct', 'other'
	created_at TIMESTAMP DEFAULT CURRENT_TIMESTAMP,
	updated_at TIMESTAMP DEFAULT CURRENT_TIMESTAMP ON UPDATE CURRENT_TIMESTAMP,
	INDEX `campaign_id` (`campaign_id`)
);
 */

export type Ad = {
  ad_id: number;
  campaign_id: number;
  smart_id?: string;
  smart_updated_at?: Date;
  name: string;
  description: string;
  start_date: string;
  end_date: string;
  budget_cents: number;
  type: AdType;
  target_urn: URN;
  goal_type: GoalType;
  user_target_language?: string[];
  user_target_geo?: string[];
  device_target_type?: string[];
  content_target_language?: string[];
  content_target_iab_category?: string[];
  placement?: string[];
  created_at?: Date;
  updated_at?: Date;
};


// todo: shouldn't this be clicks / impressions / etc??
export enum GoalType {
  wpcomPost = 'wpcom-post',
  app = 'app',
  url = 'url',
  tumblrPost = 'tsp',
}

export enum AdType {
  image = 'image',
  article = 'article',
  video = 'video',
  product = 'product',
}

export type UrnData = {
  siteId: number;
  postId: number;
}


/**
 * based on this schema
 * owner_id INT NOT NULL, -- the user that created this account
	social_login_id INT AUTO_INCREMENT PRIMARY KEY, -- the id of this social_login
	provider varchar(40) NOT NULL -- e.g. facebook
	name VARCHAR(100), -- a custom name set by the user, e.g. "Bob's Tumblr Page"
	created_at TIMESTAMP DEFAULT CURRENT_TIMESTAMP,
	expires datetime NOT NULL,
	external_id varchar(255) NOT NULL -- e.g. twitter user_id
	external_name varchar(255) DEFAULT NULL -- e.g. Dooniel Wooomsley
	external_avatar_url VARCHAR(255),
	external_display varchar(255) NOT NULL,
	access_token text NOT NULL -- the access token
	-- an owner can only have one connection to a specific external account
 */

export enum SocialLoginProvider {
  Facebook = "facebook",
  Twitter = "twitter",
  Google = "google",
  WordPress = "wordpress",
  Tumblr = "tumblr",
}

export interface SocialLogin {
  owner_id: number;
  provider: SocialLoginProvider;
  name: string;
  created_at: Date;
  expires: Date;
  external_id: string;
  external_name: string;
  external_avatar_url: string;
  external_display: string;
  access_token: string;
}

export type Pagination = {
  page: number;
  limit: number;
  page_handle?: string;
}

