export type Site = {
	ID: number
	title: string
	URL: string
	user_can_manage: boolean
	visible: boolean
	is_private: boolean
	name: string;
	description: string
	icon: {
		img: string
		ico: string
		media_id: number
	}
	logo: {
		id: number, sizes: any[], url: string;
	}
	options: {
		wordads: boolean,
		woocommerce_is_active: boolean;
		is_wpcom_store: boolean;
	}
  post_count: number
}

export type WPCOMPostAttachment = {
  URL: string,
  width: number,
  height: number
}

export type WPCOMPost = {
	ID: number,
	site_ID: number,
	modified: Date,
	title: string,
	URL: string,
	content: string,
	excerpt: string;
	like_count: number,
	featured_image: string,
	post_type: WPCOMPostType
  post_thumbnail: {
    URL: string,
    width: number,
    height: number,
    mime_type: string,
  },
  attachments: WPCOMPostAttachment[]
};

export type Media = {
  ID: number,
  URL: string,
  post_ID: number,
  file: string,
  mime_type: string,
  width: number,
  height: number,
  extension: string,
  icon: string,
  thumbnails: {
    [key: string]: string
  },
  meta: {
    links: {
      self: string,
      help: string,
      site: string,
      parent: string,
    }
  }

};

