import * as React from "react";
import { useRef, useState } from "react";
import ReactCardFlip from "react-card-flip";
import { Box, Button, Dialog, DialogActions, DialogContent, DialogTitle, IconButton } from "@mui/material";
import Card from "@mui/material/Card";
import { blue } from "@mui/material/colors";
import Alert from "@mui/material/Alert";
import Grid from "@mui/material/Grid";
import { FormContainer } from "react-hook-form-mui";
import { UploadButton } from "../utils/UploadButton";
import { useForm } from "react-hook-form";
import { styled } from "@mui/material/styles";
import CloseIcon from "@mui/icons-material/Close";
import Typography from "@mui/material/Typography";

// card flip component
type UploadImageDialogProps = {
  onClose?: () => void;
  onSuccess: (imageBlobUrl: string, imageMimeType: string) => void;
  dialogContainer?: () => HTMLDivElement | null;
};

type AddCreativeCardForm = {
  image: FormData;
};

const StyledDialogContainer = styled(Box)(() => ({
  width: 400,

  "& form": {
    minHeight: "100%",
    display: "flex",
    flexDirection: "column",
  },
  "& .upload-dropper": {
    marginTop: "20px",
    padding: "20px",
    border: "#ccc thick dashed",
    borderRadius: "20px",
    "> div": {
      // Match the ad image size here so it's a more accurate preview
      width: "300px",
      height: "140px",
      backgroundSize: "cover",
      backgroundPosition: "50% 50%",
    },
  },
}));

const UploadImageDialog: React.FC<UploadImageDialogProps> = ({ onSuccess, onClose, dialogContainer }) => {
  const [errorMessage, setErrorMessage] = useState("");
  const [imageUpload, setImageUpload] = useState<string | undefined>();
  const [imageMimeType, setImageMimeType] = useState<string>("");

  const formContext = useForm<AddCreativeCardForm>({
    defaultValues: {
      image: undefined,
    },
  });

  const handleDragOver = (e: React.DragEvent): void => {
    e.preventDefault();
  };

  const handleFileDrop = (e: React.DragEvent): void => {
    e.stopPropagation();
    e.preventDefault();

    if (e.dataTransfer?.files.length > 1) {
      setErrorMessage("Please select only one image");
      return;
    }

    const file: File = e.dataTransfer?.files[0];
    setImageUpload(window.URL.createObjectURL(file));
    setImageMimeType(file.type);
  };

  const handleUploadFile = (data: FormData, file: File) => {
    setImageUpload(window.URL.createObjectURL(file));
    setImageMimeType(file.type);
  };

  const onSuccessHandler = () => {
    if (imageUpload) {
      // TODO
      // setCreativeAssetArticleContent({ ...articleAsset.content, imageUrl: imageUpload });
      onSuccess(imageUpload, imageMimeType);
    }
  };
  return (
    <Dialog
      open={true}
      onClose={onClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
      maxWidth={false}
      container={dialogContainer}
    >
      <StyledDialogContainer>
        <FormContainer
          FormProps={{
            encType: "multipart/form-data",
          }}
          formContext={formContext}
          onSuccess={onSuccessHandler}
        >
          <DialogTitle id="alert-dialog-title">
            <Grid container>
              <Grid item xs={6}>
                Upload Creative
              </Grid>
              <Grid item xs={6}>
                <Box display="flex" justifyContent="flex-end">
                  <IconButton
                    aria-label="close"
                    onClick={onClose}
                    sx={{
                      position: "absolute",
                      right: 8,
                      top: 8,
                      color: theme => theme.palette.grey[500],
                    }}
                  >
                    <CloseIcon />
                  </IconButton>
                </Box>
              </Grid>
            </Grid>
          </DialogTitle>
          <DialogContent>
            {errorMessage && (
              <Alert severity="error" onClose={() => setErrorMessage("")}>
                {errorMessage}
              </Alert>
            )}

            <div style={{ textAlign: "center" }}>
              {imageUpload && (
                <UploadButton name="image" buttonText={"Choose another image"} onUpload={handleUploadFile} />
              )}
              <Box className="upload-dropper" onDragOver={e => handleDragOver(e)} onDrop={e => handleFileDrop(e)}>
                {imageUpload ? (
                  // Background image will closer match the created ad (becuase we will need to auto crop large images)
                  // @todo: Allow the user to change the position (cropping) themselves
                  <div style={{ backgroundImage: `url("${imageUpload}")` }} />
                ) : (
                  <Box pt={10}>
                    <Typography sx={{ mb: 1 }} textAlign="center">
                      Drag a file here or click to
                    </Typography>
                    <UploadButton name="image" onUpload={handleUploadFile} />
                  </Box>
                )}
              </Box>
            </div>
          </DialogContent>
          <DialogActions>
            <Button type={"submit"} variant={"contained"} color={"primary"}>
              Create
            </Button>
          </DialogActions>
        </FormContainer>
      </StyledDialogContainer>
    </Dialog>
  );
};

const UploadImageForm = ({ onUpload }: { onUpload?: (creativeImage: string, mimeType: string) => void }) => {
  const [showUploadDialog, setShowUploadDialog] = React.useState(false);
  const dialogContainerRef = useRef<HTMLDivElement>(null);

  return (
    <>
      <div ref={dialogContainerRef}></div>
      <ReactCardFlip flipDirection="horizontal">
        <Box>
          <Button variant="contained" color="primary" onClick={() => setShowUploadDialog(true)}>
            Replace image
          </Button>
        </Box>
        <Card
          sx={{
            backgroundColor: blue[50],
            padding: "50px 0",
            textAlign: "center",
          }}
        >
          {showUploadDialog && (
            <UploadImageDialog
              onClose={() => setShowUploadDialog(false)}
              onSuccess={(imageBlobUrl, mimeType) => {
                setShowUploadDialog(false);
                onUpload && onUpload(imageBlobUrl, mimeType);
              }}
              dialogContainer={() => dialogContainerRef.current}
            />
          )}
        </Card>
      </ReactCardFlip>
    </>
  );
};

export default UploadImageForm;
