import { Button } from "@mui/material";
import * as React from "react";
import _get from "lodash/get";

type UploadButtonProps = {
  onUpload: (data: any, file: File, urlObject: string) => void;
  buttonText?: string;
  name: string;
};

export const UploadButton = (props: UploadButtonProps) => {
  const { onUpload, buttonText, name } = props;
  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = _get(event, "target.files[0]");
    if (file) {
      const formData = new FormData();
      formData.append("image", file);
      const urlObject = window.URL.createObjectURL(file);
      onUpload(formData, file, urlObject);
    }
  };
  return (
    <>
      <input
        id="contained-button-file"
        accept="image/x-png,image/jpeg,image/gif"
        onChange={handleChange}
        style={{ display: "none" }}
        name={name}
        type="file"
      />
      <label htmlFor="contained-button-file">
        <Button variant="contained" color="primary" component="span">
          {buttonText || "Upload"}
        </Button>
      </label>
    </>
  );
};
