import { AdBuilderWidget } from "@components/adBuilderWidget";
import React, { FC, useEffect, useRef } from "react";
import { WidgetParams } from "./bootstrap";
import { useDispatch } from "react-redux";
import { setTargetUrn, setWidgetParams } from "@app/adBuilderSlice";
import { useUserAuthCheckQuery } from "@app/dspApi";
import createCache from "@emotion/cache";
import root from "react-shadow/emotion";
import { CacheProvider } from "@emotion/react";
import { DefaultTheme } from "../theme";
import { Dialog, AlertTitle, DialogContent } from "@mui/material";
import { Loading } from "@components/utils/Loading";
import Alert from "@mui/material/Alert";
import { ThemeProvider } from "@mui/material/styles";
import { css, Global } from "@emotion/react";
import WidgetStyles from "./components/WidgetStyles";

type Props = {
  params: WidgetParams;
};

function Widget({ params }: Props) {
  const dispatch = useDispatch();
  const styleRef = useRef<HTMLStyleElement>(null);
  const dialogContainerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    dispatch(setWidgetParams(params));
    dispatch(setTargetUrn(params.urn));
  }, []);

  const { data, error, isLoading, refetch } = useUserAuthCheckQuery();

  const cache = createCache({
    key: "css",
    prepend: true,
    container: styleRef.current as HTMLStyleElement,
  });

  const ShadowDom = root.div as FC<{ id: string }>;

  const [closed, setClosed] = React.useState(false);
  // track if the widget is closed
  const onClose = () => {
    setClosed(true);
  };

  if (isLoading) {
    // TODO better UI for this
    return (
      <Dialog open={true}>
        <DialogContent>
          <Loading />
        </DialogContent>
      </Dialog>
    );
  }
  if (data && !data.exists) {
    // render an error message
    return <Alert severity="error">User does not exist</Alert>;
  }
  if (error) {
    console.warn("token not valid", error);

    // TODO better UI for this
    return (
      <Alert severity="error">
        <AlertTitle>Error</AlertTitle>
        {(error as any)?.data?.errorMessage}
        {(error as any)?.status === "FETCH_ERROR" && "There was an error communicating with the server"}
      </Alert>
    );
  }

  return (
    <>
      {!closed && (
        <CacheProvider value={cache}>
          {/* IMPORTANT NOTE, this is where external styles get injected,
              if you want to change the id, please update craco.config.ts as well */}
          <ShadowDom id="wpcom-dsp-widget-shadow-dom">
            <WidgetStyles />
            <ThemeProvider theme={DefaultTheme}>
              <style ref={styleRef}></style>
              <Global
                styles={css`
                  :root {
                    margin: 0;
                    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
                      "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
                    -webkit-font-smoothing: antialiased;
                    -moz-osx-font-smoothing: grayscale;
                  }

                  code {
                    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
                  }
                `}
              />
              <div ref={dialogContainerRef}></div>
              <AdBuilderWidget onClose={onClose} dialogContainer={() => dialogContainerRef.current} />
            </ThemeProvider>
          </ShadowDom>
        </CacheProvider>
      )}
    </>
  );
}

export default Widget;
