import * as React from "react";
import Grid from "@mui/material/Grid";
import { List, Slide, SlideProps, Typography } from "@mui/material";
import { RootState } from "@app/store";
import { connect, ConnectedProps } from "react-redux";
import { FormResultField } from "./FormResultField";
import { AdPreviewWrapper } from "@components/adBuilder/components/AdPreviewWrapper";
import Ad_300_250 from "@components/creative/adTemplates/ad_300_250";

const slideProps: SlideProps = {
  easing: {
    enter: "linear",
    exit: "inherit",
  },
  direction: "left",
  mountOnEnter: true,
  unmountOnExit: true,
} as SlideProps;

type Props = ConnectedProps<typeof connector>;

export const StepConfirmAdComponent: React.FC<Props> = ({ article, paymentMethod }) => {
  return (
    <Slide in {...slideProps}>
      <Grid item xs={12}>
        <Typography variant="h4" sx={{ mb: 2, mt: { sm: 2, lg: 0 } }} textAlign="center">
          Let's review the ad 
        </Typography>

        <List
          sx={{
            width: "100%",
            bgcolor: "background.paper",
            overflow: " auto",
          }}
        >
          <FormResultField value={article.clickUrl} label="Target URL" />
          <FormResultField value={paymentMethod} label="Payment Method" />
        </List>
        <Grid container spacing={0} direction="column" alignItems="center" sx={{ mt: 3 }}>
          <Grid item xs={4}>
            <AdPreviewWrapper>
              <Ad_300_250 {...article} />
            </AdPreviewWrapper>
          </Grid>
        </Grid>
      </Grid>
    </Slide>
  );
};

const mapDispatchToProps = {};
const mapStateToProps = (state: RootState) => ({
  paymentMethod: state.adBuilder.paymentMethod,
  article: state.adBuilder.article,
  formStep: state.adBuilder.formStep,
});
const connector = connect(mapStateToProps, mapDispatchToProps);
export const StepConfirmAd = connector(StepConfirmAdComponent);
