import React, { Ref, useState } from "react";
import "@fontsource/source-sans-pro";
import "@fontsource/libre-baskerville";
import "@fontsource/roboto/300.css";
import "@fontsource/roboto/400.css";
import "@fontsource/roboto/500.css";
import "@fontsource/roboto/700.css";
import {
  Alert,
  Box,
  Button,
  Container,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import { StepWidgetIntro } from "./components/StepWidgetIntro";
import { WidgetStepper } from "./components/WidgetStepper";
import { FormSteps } from "@type/dsp";
import { connect, ConnectedProps } from "react-redux";
import {
  createFullCampaignWidgetModeThunk,
  setAdBudgetCentsPerDay,
  setAdEndDate,
  setAdStartDate,
  setFormStep,
  setNextButtonEnabled,
} from "@app/adBuilderSlice";
import { RootState } from "@app/store";
import { StepFinish } from "@components/adBuilder/components/StepFinish";
import { StepBudgetTiming } from "@components/adBuilder/components/StepBudgetTiming";
import { StepSiteTargeting } from "@components/adBuilder/components/StepSiteTargeting";
import { StepPaymentMethod } from "@components/adBuilder/components/StepPaymentMethod";
import { StepPreviewCreative } from "@components/adBuilder/components/StepPreviewCreative";
import { StepConfirmAd } from "@components/adBuilder/components/StepConfirmAd";
import { StepPay } from "@components/adBuilder/components/StepPay";

type OwnProps = {
  onClose?: () => void;
  onSuccess?: (CampaignExtended: any) => void; // to be defined the naturality of this whole object
  dialogContainer?: () => HTMLDivElement | null;
};

type Props = ConnectedProps<typeof connector> & OwnProps;

const formSteps = [
  FormSteps.intro,
  FormSteps.previewCreative,
  FormSteps.siteTargeting,
  FormSteps.budgetTiming,
  FormSteps.pay,
  FormSteps.confirmAd,
  FormSteps.finish,
];

function getPreviousStep(step: FormSteps) {
  const index = formSteps.indexOf(step);
  return formSteps[index - 1];
}

function getNextStep(step: FormSteps) {
  const index = formSteps.indexOf(step);
  return formSteps[index + 1];
}

export const AdBuilderComponent: React.FC<Props> = ({
  formStep,
  setFormStep,
  onClose,
  onSuccess,
  startDate,
  endDate,
  dailyBudgetInCents,
  setAdStartDate,
  setAdEndDate,
  setAdBudgetCentsPerDay,
  nextButtonEnabled,
  createFullCampaignWidgetModeThunk,
  setNextButtonEnabled,
  adBuilder,
  dialogContainer,
}) => {
  const [errorMessage, setErrorMessage] = useState("");

  return (
    <Dialog
      container={dialogContainer}
      open={true}
      onClose={onClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
      fullWidth={true}
      maxWidth="md"
      sx={{
        "& > .MuiDialog-container": {
          "& > .MuiPaper-root": {
            height: {
              xs: "calc(100vh - 64px)",
              sm: "800px",
              lg: "800px",
            },
          },
        },
      }}
    >
      {formStep !== FormSteps.intro ? (
        <DialogTitle
          sx={{
            mb: 2,
          }}
          id="dialog-title"
        >
          <Grid container>
            {/*<Grid item xs={6}>
                Boost the Post
              </Grid>*/}
            <Grid item xs={6}>
              <Box display="flex" justifyContent="flex-end">
                <IconButton
                  aria-label="close"
                  onClick={onClose}
                  sx={{
                    position: "absolute",
                    right: 8,
                    top: 8,
                    color: theme => theme.palette.grey[500],
                  }}
                >
                  <CloseIcon />
                </IconButton>
              </Box>
            </Grid>
            <Grid item xs={12} sx={{ mt: 3 }}>
              {/* <MobileWidgetStepper formStep={formStep}/> */}
              <WidgetStepper formStep={formStep} />
            </Grid>
          </Grid>
        </DialogTitle>
      ) : (
        <Grid container>
          <Grid item xs={12}>
            <Box display="flex" justifyContent="flex-end">
              <IconButton
                aria-label="close"
                onClick={onClose}
                sx={{
                  position: "absolute",
                  right: 8,
                  top: 8,
                  color: theme => theme.palette.grey[500],
                }}
              >
                <CloseIcon />
              </IconButton>
            </Box>
          </Grid>
        </Grid>
      )}

      <DialogContent>
        {errorMessage && (
          <Alert severity="error" onClose={() => setErrorMessage("")}>
            {errorMessage}
          </Alert>
        )}
        <Container
          sx={{
            overflow: "hidden",
            height: "100%",
          }}
        >
          {formStep === FormSteps.intro && (
            <StepWidgetIntro
              onClickGetStarted={() => {
                setFormStep(FormSteps.previewCreative);
                setNextButtonEnabled(false);
              }}
            />
          )}
          {formStep === FormSteps.previewCreative && <StepPreviewCreative />}
          {formStep === FormSteps.siteTargeting && (
            <StepSiteTargeting
              onSiteTargetingSelected={(siteTarget: string) => {
                console.warn("selected site targeting", siteTarget);
                // go to the next step
                setFormStep(FormSteps.budgetTiming);
              }}
              onClickPrevious={function (): void {
                console.warn("clicked previous");
                setFormStep(FormSteps.previewCreative);
              }}
            />
          )}
          {formStep === FormSteps.budgetTiming && (
            <StepBudgetTiming
              onSubmit={values => {
                console.warn("submit budget timing", values);
                // go to the next step
                setFormStep(FormSteps.paymentMethod);
              }}
              onClickPrevious={function (): void {
                console.warn("clicked previous");
                setFormStep(FormSteps.siteTargeting);
              }}
              startDate={startDate}
              endDate={endDate}
              dailyBudgetInCents={dailyBudgetInCents}
              setAdStartDate={setAdStartDate}
              setAdEndDate={setAdEndDate}
              setAdBudgetCentsPerDay={setAdBudgetCentsPerDay}
            />
          )}
          {formStep === FormSteps.pay && <StepPay />}
          {formStep === FormSteps.confirmAd && <StepConfirmAd />}
          {formStep === FormSteps.finish && <StepFinish />}
        </Container>
      </DialogContent>
      <DialogActions
        sx={{
          flex: "inherit",
        }}
      >
        <Grid container>
          <Grid item xs={6}>
            {formStep !== FormSteps.intro && formStep !== FormSteps.finish && (
              <Button
                color="secondary"
                disabled={getPreviousStep(formStep) === undefined}
                onClick={() => {
                  setFormStep(getPreviousStep(formStep));
                }}
              >
                Previous
              </Button>
            )}
          </Grid>
          <Grid item xs={6}>
            <Box display="flex" justifyContent="flex-end">
              {formStep === FormSteps.confirmAd && (
                <Button
                  type={"submit"}
                  variant={"contained"}
                  color={"primary"}
                  onClick={async () => {
                    setFormStep(getNextStep(formStep));
                    const campaignCreated = await createFullCampaignWidgetModeThunk(adBuilder);
                    onSuccess && onSuccess(campaignCreated);
                  }}
                >
                  Create
                </Button>
              )}
              {getNextStep(formStep) !== undefined && formStep !== FormSteps.intro && formStep !== FormSteps.confirmAd && (
                <Button
                  variant="contained"
                  color="primary"
                  disabled={!nextButtonEnabled}
                  onClick={() => {
                    setFormStep(getNextStep(formStep));
                  }}
                >
                  Continue
                </Button>
              )}
            </Box>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
};

const mapDispatchToProps = {
  setFormStep,
  createFullCampaignWidgetModeThunk,
  setAdStartDate,
  setAdEndDate,
  setAdBudgetCentsPerDay,
  setNextButtonEnabled,
};
const mapStateToProps = (state: RootState) => ({
  formStep: state.adBuilder.formStep,
  dailyBudgetInCents: state.adBuilder.ad.budget_cents,
  startDate: state.adBuilder.ad.start_date,
  endDate: state.adBuilder.ad.end_date,
  adBuilder: state.adBuilder,
  nextButtonEnabled: state.adBuilder.nextButtonEnabled,
});
const connector = connect(mapStateToProps, mapDispatchToProps);
export const AdBuilderWidget = connector(AdBuilderComponent);
