import * as React from "react";
import Grid from "@mui/material/Grid";
import { InputLabel, Slide, SlideProps, Typography } from "@mui/material";
import MultipleSelect from "@components/adBuilder/components/MultipleSelect";
import {
  setTargetLocations,
  setTargetSiteLanguages,
  setTargetSiteTopics,
  setDeviceTargetTypes,
} from "@app/adBuilderSlice";
import { RootState } from "@app/store";
import { connect } from "react-redux";
import {
  useGetAvailableLocationsQuery,
  useGetAvailableSiteTargetLanguagesQuery,
  useGetAvailableSiteTargetTopicsQuery,
} from "@app/dspApi";
import CircularProgress from "@mui/material/CircularProgress";
import { DeviceTargetTypes } from "@type/dsp";

const slideProps: SlideProps = {
  easing: {
    enter: "linear",
    exit: "inherit",
  },
  direction: "left",
  mountOnEnter: true,
  unmountOnExit: true,
} as SlideProps;

type Props = {
  selectedLanguages?: string[];
  selectedLocations?: string[];
  selectedDevices?: string[];
  selectedTopics?: string[];
  setTargetLocations: (locations: string[]) => void;
  setDeviceTargetTypes: (locations: string[]) => void;
  setTargetSiteLanguages: (languages: string[]) => void;
  setTargetSiteTopics: (topics: string[]) => void;
  onSiteTargetingSelected: (step: string) => void;
  onClickPrevious: () => void;
};
// TODO trigger the callback
export const StepSiteTargetingComponent: React.FC<Props> = ({
  selectedLanguages,
  selectedTopics,
  selectedLocations,
  selectedDevices,
  setTargetSiteLanguages,
  setDeviceTargetTypes,
  setTargetSiteTopics,
  setTargetLocations,
  onSiteTargetingSelected,
  onClickPrevious,
}) => {
  // TODO get from backend
  const { data: languagesData, isFetching: isFetchingLanguages } = useGetAvailableSiteTargetLanguagesQuery();
  const { data: topicsData, isFetching: isFetchingTopics } = useGetAvailableSiteTargetTopicsQuery();
  const { data: locationsData, isFetching: isFetchingLocations } = useGetAvailableLocationsQuery();
  const rawLanguages = isFetchingLanguages ? [] : languagesData ?? [];
  const languages = rawLanguages.map(language => ({ value: language.id, text: language.text }));
  const rawTopics = isFetchingTopics ? [] : topicsData ?? [];
  const rawLocations = isFetchingLocations ? [] : locationsData ?? [];
  const topics = rawTopics.map(topic => ({ value: topic.code, text: topic.text }));
  const locations = rawLocations.map(location => ({ value: location.id, text: location.text }));
  const deviceTypes: { text: string; value: DeviceTargetTypes }[] = [
    { text: "Desktop", value: DeviceTargetTypes.desktop },
    { text: "Mobile", value: DeviceTargetTypes.mobile },
    { text: "Tablet", value: DeviceTargetTypes.tablet },
  ];

  return (
    <Slide in={true} {...slideProps}>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <Typography variant={"h3"}>Location</Typography>
          <Typography variant={"subtitle2"}>Where do you want to promote your post?</Typography>

          <InputLabel sx={{ mt: 2 }}>Country</InputLabel>

          {isFetchingTopics ? (
            <CircularProgress />
          ) : (
            <MultipleSelect
              values={selectedLocations ?? []}
              options={locations}
              onChange={setTargetLocations}
              placeholder="All"
            />
          )}
        </Grid>

        <Grid item xs={12}>
          <InputLabel>Language</InputLabel>

          {isFetchingTopics ? (
            <CircularProgress />
          ) : (
            <MultipleSelect
              values={selectedLanguages ?? []}
              options={languages}
              onChange={setTargetSiteLanguages}
              placeholder="All"
            />
          )}
        </Grid>

        <Grid item xs={12}>
          {isFetchingLanguages ? (
            <CircularProgress />
          ) : (
            <>
              <InputLabel id="interests">Interests</InputLabel>
              <Typography variant={"subtitle2"}>Add a broad range of interests as tags.</Typography>

              <MultipleSelect
                values={selectedTopics ?? []}
                options={topics}
                onChange={setTargetSiteTopics}
                placeholder="All"
              />
            </>
          )}
        </Grid>

        <Grid item xs={12}>
          <InputLabel id="devices">Devices</InputLabel>
          <MultipleSelect
            values={selectedDevices ?? []}
            options={deviceTypes}
            onChange={setDeviceTargetTypes}
            placeholder="All"
          />
        </Grid>
      </Grid>
    </Slide>
  );
};

const mapDispatchToProps = { setTargetSiteLanguages, setTargetSiteTopics, setTargetLocations, setDeviceTargetTypes };
const mapStateToProps = (state: RootState) => {
  return {
    selectedLanguages: state.adBuilder.ad.content_target_language,
    selectedTopics: state.adBuilder.ad.content_target_iab_category,
    selectedLocations: state.adBuilder.ad.user_target_geo,
    selectedDevices: state.adBuilder.ad.device_target_type,
  };
};
const connector = connect(mapStateToProps, mapDispatchToProps);
export const StepSiteTargeting = connector(StepSiteTargetingComponent);
