import React from "react";
declare let window: any;

// We inject our styles in .css files under the Shadow Dom, instead of the common header
// One drawback is when the widget is destroyed and re-mounted, webpack won't inject the css again.
// So we would want to cache the styles on initial inject and for subsequent re-mount
// we will use this component to re-render our styles. There is a chance on initial render,
// we get duplicate <style> tags, but that won't really hurt, but missing a <style> tag would be fatal.
// If later we found more elegant approach, we can change this.
// Note @font-face styles are injected to header, so no need to re-render. (@font-face won't work
// under ShadowDom, it won't fire to load the font files.
function WidgetStyles() {
  const styles = window._dsp_widget_styles
    .filter((el: HTMLElement) => el.innerHTML && !el.innerHTML.includes("@font-face"))
    .map((el: HTMLElement) => el.innerHTML)
    .join("");
  return styles.length ? <style>{styles}</style> : null;
}

export default WidgetStyles;
