let baseUrl = BASE_URI;

const updateBaseUrl = (widgetParams: WidgetParams) => {
  const apiHost = widgetParams.apiHost || process.env.REACT_APP_DSP_API_HOST || "";
  const apiPrefix = widgetParams.apiPrefix || process.env.REACT_APP_DSP_API_PREFIX || "";
  baseUrl = `${apiHost}${apiPrefix}${BASE_URI}`;
};

const headers: { [key: string]: string } = {
  Accept: "application/json",
  "Content-Type": "application/json",
};

const updateHeaders = (token: string) => {
  if (!token) {
    return;
  }
  if (token.startsWith("rlt-")) {
    // special handling for 'rlt-' token
    headers.Authorization = `X_WPCOM_RLT ${token}`;
  } else {
    headers.Authorization = `Bearer ${token}`;
  }
};

export const fetchImageAsBlob = async (imageUrl: string): Promise<Blob | null> => {
  // proxy so we can get bytes from images without CORS headers
  const response = await fetch(`${baseUrl}/image/${encodeURIComponent(imageUrl)}`, {
    method: "GET",
    headers: {
      accept: "image/*",
      authorization: headers.Authorization,
    },
  });
  return await response.blob();
};

const createCampaign = async (article: CreativeAssetArticleContent, ad: Ad): Promise<Campaign> => {
  const createCampaignRequest: CreateCampaignRequest = {
    name: article.title,
    description: "",
    start_date: ad.start_date,
    end_date: ad.end_date,
  };

  const createCampaignResponse = await fetch(`${baseUrl}/campaigns`, {
    method: "POST",
    body: JSON.stringify(createCampaignRequest),
    headers,
  });

  const createdCampaign = (await createCampaignResponse.json()) as CampaignResponse;

  if (!createCampaignResponse.ok) {
    // const reason = createdAd.errorMessage || "unknown error";
    throw new Error("Creating campaign failed");
  }
  return createdCampaign;
};

const createAd = async (
  article: CreativeAssetArticleContent,
  ad: Ad,
  createdCampaign: Campaign,
  widgetParams: WidgetParams
) => {
  const createAdRequest: CreateAdRequest = {
    campaign_id: createdCampaign.campaign_id,
    name: article.title,
    description: "",
    start_date: ad.start_date,
    end_date: ad.end_date,
    budget_cents: ad.budget_cents,
    type: ad.type,
    goal_type: ad.goal_type,
    target_urn: widgetParams.urn,
    user_target_geo: ad.user_target_geo,
    device_target_type: ad.device_target_type,
    content_target_language: ad.content_target_language,
    content_target_iab_category: ad.content_target_iab_category,
  };

  const createAdResponse = await fetch(`${baseUrl}/campaigns/${createdCampaign.campaign_id}/ads`, {
    method: "POST",
    body: JSON.stringify(createAdRequest),
    headers,
  });

  const createdAd = (await createAdResponse.json()) as AdResponse;

  if (!createAdResponse.ok) {
    // const reason = createdAd.errorMessage || "unknown error";
    throw new Error("Creating ad failed");
  }

  return createdAd;
};

export const readBlob = async (blob: Blob): Promise<string> => {
  const reader = new FileReader();
  reader.readAsDataURL(blob);
  return new Promise((resolve, reject) => {
    reader.onloadend = () => {
      const base64data = reader.result;
      resolve(base64data as string);
    };
    reader.onerror = error => {
      reject(error);
    };
  });
};

const createCreative = async (
  article: CreativeAssetArticleContent,
  createdCampaign: Campaign,
  createdAd: Ad,
  widgetParams: WidgetParams
) => {
  // hack so we can read the blob as base64
  const imageRaw = await fetch(article.imageUrl);
  const blob = await imageRaw.blob();

  // const blob = new Blob([imageBlob], { type: article.imageMimeType });

  const imageBase64 = await readBlob(blob);

  // under the hood this creates a creative of type "article" with two assets, one image and one article
  // there's some shenanigans in order to upload the image with the request
  const createCreativeRequest: CreateCreativeRequest = {
    campaign_id: createdCampaign.campaign_id,
    ad_id: createdAd.ad_id,
    name: article.title,
    description: "",
    type: CreativeType.article,
    urn: widgetParams.urn,
    width: 300, // todo: this should be defined differently. We should create presets of configurations
    height: 250,
    assets: [
      {
        mime_type: "application/json",
        content: {
          title: article.title,
          snippet: article.snippet,
          clickUrl: article.clickUrl,
          imageUrl: "assets://featured.jpg", //special URL
        },
      },
      {
        file_name: "featured.jpg",
        mime_type: article.imageMimeType || "",
        content: imageBase64,
      },
    ],
  };

  const creativeResponse = await fetch(
    `${baseUrl}/campaigns/${createdCampaign.campaign_id}/ads/${createdAd.ad_id}/creatives`,
    {
      method: "POST",
      body: JSON.stringify(createCreativeRequest),
      headers,
    }
  );

  if (!creativeResponse.ok) {
    throw new Error("Uploading assets failed");
  }

  const createdCreative = await creativeResponse.json();
  return createdCreative;
};

const submitForModeration = async (createdCampaign: Campaign) => {
  const submitResponse = await fetch(`${baseUrl}/campaigns/${createdCampaign.campaign_id}/submit`, {
    method: "POST",
    body: JSON.stringify({}),
    headers,
  });

  const submit = await submitResponse.json();

  if (!submitResponse.ok) {
    throw new Error("Submitting for approval failed");
  }
  return submit;
};

export const createFullCampaignWidgetModeThunk = createAsyncThunk(
  "adBuilder/createFullCampaign",
  async (request: AdBuilderState, thunkAPI) => {
    const { ad, article, widgetParams } = request;

    try {
      // create the campaign
      const createdCampaign = await createCampaign(article, ad);
      await thunkAPI.dispatch(setCampaignCreated(true));
      console.warn("created campaign", createdCampaign);

      // create the ad
      const createdAd = await createAd(article, ad, createdCampaign, widgetParams);
      await thunkAPI.dispatch(setAdCreated(true));
      console.warn("created ad", createdAd);

      // create the creative
      const createdCreative = await createCreative(article, createdCampaign, createdAd, widgetParams);
      await thunkAPI.dispatch(setAssetsUploaded(true));
      console.warn("creative response", createdCreative);

      // now submit for moderation
      const submit = await submitForModeration(createdCampaign);
      await thunkAPI.dispatch(setSubmitted(true));
      console.warn("submit response", submit);

      await thunkAPI.dispatch(dspApi.util.invalidateTags(["Creatives", "Ads", "Campaigns"]));

      await thunkAPI.dispatch(setCampaignCreatedId(createdCampaign.campaign_id));
      return {
        campaign: createdCampaign,
        ad: createdAd,
        creative: createdCreative,
        submit: submit,
      };
    } catch (e) {
      console.error("error creating campaign", e);
      // if the error has a message prop, display the message
      if (Object.hasOwnProperty.call(e, "message")) {
        const { message } = e as { message: string };
        await thunkAPI.dispatch(setCreateCampaignErrorMessage(message));
      }
      // delete the campaign in the remote possibility that the campaign was created but no file was attached
      // TODO LATER
      // if (campaign_id) {
      //   await fetch(`${baseUrl}/campaigns/${campaign_id}`, {
      //     method: "DELETE",
      //   });
      //   thunkAPI.dispatch(dspApi.util.invalidateTags(["Creatives", "Ads", "Campaigns"]));
      // }
    }
  }
);
